.console header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.556rem;
  background-color: rgba(0, 0, 0, 0.04);
  font-size: 0.778rem;
}
.console header a {
  color: rgba(0, 0, 0, 0.55);
}
.console header .main {
  font-size: 1.111rem;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.55);
  line-height: 2.611rem;
}
.console header .site-dropdown,
.console header .right {
  flex: 1;
}
.console header .right {
  text-align: right;
}
.console header .right .lang-dropdown,
.console header .right .help-dropdown {
  display: inline-block;
  margin: 0 10px;
}
.console header .anticon-down {
  margin-left: 5px;
}
.console .console-container {
  max-width: 33.333rem;
  margin: 0 auto;
  padding: 1rem 1.556rem;
}
.console .console-container .tip {
  padding-top: 0.2rem;
  color: #9ba0a3;
  font-size: 0.722rem;
}
.console .console-container .ant-alert {
  box-shadow: -6px 0 0 0 #5aaeed, 0 0 0 0 transparent;
}
.console .console-container .ant-alert .bulleted {
  color: #2c97e8;
  padding-left: 1rem;
}
.console .console-container .ant-alert .bulleted li:before {
  user-select: none;
  pointer-events: none;
  position: absolute;
  top: auto;
  left: auto;
  font-weight: 400;
  margin-left: -0.667rem;
  content: "•";
  opacity: 1;
  color: inherit;
  vertical-align: top;
}
