.banner-content {
  text-align: center;
}
.banner-content h1 {
  margin: 40px 0;
}
.banner-content .font-logo {
  font-size: 60px;
}
.banner-content .h3 {
  font-size: 36px;
}
.banner-content a {
  color: unset;
}
.myHomeSwiper {
  height: calc(100vh - 3.556rem);
}
.myHomeSwiper .swiper-slide {
  text-align: center;
  font-size: 18px;
  width: 100%;
  background-color: #2c97e8;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.myHomeSwiper .swiper-slide .banner {
  width: 100%;
  height: 100%;
  text-align: center;
}
.myHomeSwiper .swiper-slide .banner h1 {
  margin: 40px 0;
}
.myHomeSwiper .swiper-slide .banner .font-logo {
  font-size: 60px;
}
.myHomeSwiper .swiper-slide .banner .h3 {
  font-size: 36px;
}
.myHomeSwiper .swiper-slide .banner a {
  color: unset;
}
.myHomeSwiper .swiper-slide .banner1 {
  background: #ffffff url('../../../assets/banner1.svg') no-repeat center bottom / 1600px;
}
.myHomeSwiper .swiper-slide .banner2 {
  background: #ffffff url('../../../assets/banner2.svg') no-repeat center bottom / 1600px;
}
.myHomeSwiper .swiper-pagination-bullet {
  background: #948f8f;
  opacity: 1;
  width: 80px;
  max-width: 15%;
  height: 6px;
  border-radius: 0;
  outline: 0;
}
.myHomeSwiper .swiper-pagination-bullet-active {
  background: #2c97e8 !important;
}
.section-wrapper {
  width: 100%;
}
.section-wrapper .section-contain {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 4.8em 20px;
}
.section-wrapper .section-contain .text {
  width: 55%;
}
.section-wrapper .section-contain .text .title {
  font-size: 36px;
  color: #455d82;
}
.section-wrapper .section-contain .text p {
  font-size: 18px;
}
.section-wrapper .section-contain .serverImg {
  width: 40%;
  background: url("../../../assets/server.svg") no-repeat center center / contain;
  height: 280px;
}
.server {
  margin-top: 50px;
  background: #FFFFFF;
  padding: 4.8em 0;
}
.server > .title {
  font-size: 36px;
  color: #455d82;
  text-align: center;
}
.server .server-item {
  display: flex;
  flex-wrap: wrap;
}
.server .server-item div {
  min-width: 40%;
  border: 1px solid rgba(186, 172, 172, 0.2);
  border-radius: 0.8em;
  padding: 2em;
  margin: 40px 20px 0 20px;
  flex: 1;
}
.server .server-item div svg {
  width: 60px;
  height: 60px;
  color: #2c97e8;
}
.server .server-item div .title {
  color: #455d82;
  font-size: 24px;
  line-height: 2;
}
.server .server-item div p {
  color: #373d41;
  font-size: 16px;
  margin-top: 16px;
}
.section-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 20px;
}
.section-container .platformImg {
  width: 40%;
  background: url("../../../assets/platform.svg") no-repeat center center / contain;
  height: 280px;
}
.section-container .text {
  width: 55%;
  font-size: 18px;
}
.section-container .text .title {
  font-size: 36px;
  color: #455d82;
  text-align: center;
}
.section-container .text .iconGroup {
  display: flex;
  justify-content: space-between;
}
.section-container .text .iconGroup .icon {
  flex: 1;
  width: 45px;
  height: 45px;
  max-width: 10%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.section-container .text .iconGroup .js {
  background-image: url("../../../assets/js.svg");
}
.section-container .text .iconGroup .java {
  background-image: url("../../../assets/java.svg");
}
.section-container .text .iconGroup .swift {
  background-image: url("../../../assets/swift.svg");
}
.section-container .text .iconGroup .python {
  background-image: url("../../../assets/python.svg");
}
.section-container .text .iconGroup .php {
  background-image: url("../../../assets/php.svg");
}
.section-container .text .iconGroup .objc {
  background-image: url("../../../assets/objc.svg");
}
.section-container .text .iconGroup .net {
  background-image: url("../../../assets/net.svg");
}
.section {
  width: 100%;
  background: #ffffff;
  padding-bottom: 48px ;
}
.section .Aceditor {
  background: #FFFFFF;
  padding-top: 50px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.section .Aceditor .editor {
  width: 100%;
  max-width: 770px;
  border: 1px solid #def1ff;
}
.section .Aceditor .editor .program {
  display: flex;
  list-style: none;
  background: #def1ff;
  padding: 20px;
}
.section .Aceditor .editor .program li {
  flex: 1;
  text-align: center;
  padding: 5px 0;
  margin: 0 15px;
  cursor: pointer;
}
.section .Aceditor .editor .program li.active {
  font-weight: 400;
  color: #fff;
  border: 1px solid transparent;
  border-radius: 14px;
  background-color: #2c97e8;
}
.section .Aceditor .editor-doc {
  flex: 0 1 380px;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 16px;
}
.section .Aceditor .editor-doc li {
  list-style: none;
  border: 1px solid #def1ff;
  padding: 0.5em 1em;
  line-height: 2;
  margin: 0 0 -1px;
  position: relative;
  cursor: pointer;
}
.store {
  display: flex;
  padding: 40px;
  align-items: center;
}
.store .storeImg {
  width: 40%;
  height: 280px;
  background: url("../../../assets/storage.svg") no-repeat center center / contain;
}
.store .text {
  width: 55%;
  font-size: 18px;
}
.store .text .title {
  font-size: 36px;
  color: #455d82;
  text-align: center;
}
.global-section-contain {
  width: 100%;
  padding-bottom: 40px;
}
.global-section-contain .global-section {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.global-section-contain .global-section > div {
  display: flex;
  flex: 1;
  padding: 20px;
  font-size: 18px;
}
.global-section-contain .global-section > div i {
  width: 58px;
  min-width: 58px;
  height: 58px;
  margin: auto 0;
}
.global-section-contain .global-section > div p {
  padding-left: 10px;
  margin: 0;
}
.global-section-contain .global-section > div .map {
  background: url("../../../assets/map.svg") no-repeat center / contain;
}
.global-section-contain .global-section > div .shield {
  background: url("../../../assets/shield.svg") no-repeat center / contain;
}
.home-price {
  width: 100%;
  padding: 4.8em 0;
  background-color: #fff;
}
.home-price .price-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
}
.home-price .price-section .price-img {
  width: 40%;
}
.home-price .price-section .text {
  width: 55%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.home-price .price-section .text .title {
  font-size: 32px;
  color: #455d82;
  font-weight: 400;
}
.versions {
  width: 100%;
}
.versions .versions-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}
.versions .versions-container section {
  padding: 20px;
}
