.AccountHead .logo {
  color: #fff;
  background-color: #2c97e8;
  display: block;
  text-align: center;
  font-size: 1.111rem;
  line-height: 2.611rem;
  font-weight: bold;
  cursor: pointer;
}
.AccountHead .site-layout {
  background: #FFFFFF;
}
.AccountHead .site-layout .site-layout-background {
  background-color: rgba(0, 0, 0, 0.04);
  border-bottom: 1px solid rgba(34, 36, 38, 0.06);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.AccountHead .site-layout .site-layout-background .accountHeadLeft,
.AccountHead .site-layout .site-layout-background .accountHeadRight {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.AccountHead .site-layout .site-layout-background .accountHeadLeft > div,
.AccountHead .site-layout .site-layout-background .accountHeadRight > div {
  width: 3.611rem;
}
.AccountHead .site-layout .site-layout-background .accountHeadLeft > div svg,
.AccountHead .site-layout .site-layout-background .accountHeadRight > div svg {
  display: inline-block;
  margin: 0 5px;
}
.AccountHead .ant-menu .ant-menu-item-selected {
  color: #5aaeed;
  background-color: transparent !important;
  position: relative;
}
.AccountHead .ant-menu .ant-menu-item-selected:after {
  content: "";
  position: absolute;
  width: 4px;
  height: 60%;
  left: 0.667rem;
  top: 20%;
  background: #2c97e8;
}
.AccountHead .token-container,
.AccountHead .email-container,
.AccountHead .profile-container {
  padding: 1.111rem 1.556rem;
}
.AccountHead .token-container .ant-alert,
.AccountHead .email-container .ant-alert,
.AccountHead .profile-container .ant-alert {
  border-radius: 0;
  background-color: #f4faff;
  border-color: #f4faff;
  color: #2c97e8;
  box-shadow: -6px 0 0 0 #5aaeed, 0 0 0 0 transparent;
}
.AccountHead .token-container .ant-space,
.AccountHead .email-container .ant-space,
.AccountHead .profile-container .ant-space {
  width: 100%;
}
.AccountHead .token-container .ant-modal-body,
.AccountHead .email-container .ant-modal-body,
.AccountHead .profile-container .ant-modal-body {
  padding: 0;
}
.AccountHead .token-container .ant-modal-body .modal-question,
.AccountHead .email-container .ant-modal-body .modal-question,
.AccountHead .profile-container .ant-modal-body .modal-question {
  padding: 1.333rem;
  background: rgba(153, 153, 153, 0.1);
}
.AccountHead .token-container .ant-modal-body .modal-main,
.AccountHead .email-container .ant-modal-body .modal-main,
.AccountHead .profile-container .ant-modal-body .modal-main {
  padding: 0 1.333rem 0.667rem;
}
.AccountHead .token-container .ant-modal-body .modal-main .ant-tag,
.AccountHead .email-container .ant-modal-body .modal-main .ant-tag,
.AccountHead .profile-container .ant-modal-body .modal-main .ant-tag {
  text-transform: uppercase;
  cursor: help;
}
.AccountHead .token-container .ant-modal-body .modal-main .ant-tag:hover,
.AccountHead .email-container .ant-modal-body .modal-main .ant-tag:hover,
.AccountHead .profile-container .ant-modal-body .modal-main .ant-tag:hover {
  color: #ff8c2c;
}
