.section-support {
  background: #FFFFFF;
}
.section-support .support {
  max-width: 66.667rem;
  margin: 0 auto;
}
.section-support .support .title {
  padding: 4.833rem 0;
}
.section-support .support .title h1 {
  color: #2c97e8;
  font-size: 2.333rem;
}
.section-support .support .list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}
.section-support .support .list .list-item {
  flex: 1 1 19.444rem;
  padding-bottom: 2.5rem;
}
.section-support .support .list .list-item h1 {
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 4px;
  color: #373d41;
}
.section-support .support .list .list-item a {
  display: block;
  color: #808a8e;
  font-size: 18px;
  line-height: 2;
  cursor: pointer;
}
.section-support .support .list .list-item a:hover {
  color: #2c97e8;
}
.section-support .support .section-contain {
  margin: 0;
  color: #9ba0a3;
}
.section-support .support .section-contain .section-back {
  cursor: pointer;
}
.section-support .support .section-contain .calc {
  margin: 0.556rem 0 0;
  padding-bottom: 1.333rem;
}
.section-support .support .section-contain .question .title {
  color: #373d41;
  line-height: 2;
  margin-top: 10px;
  padding: 0;
}
.section-support .support .section-contain .question .question-cards {
  width: 19.667rem;
  border: 1px solid rgba(186, 172, 172, 0.2);
  border-radius: 0.8em;
  padding: 1em 1.5em;
  transition: all 0.2s ease, border-color 0.2s ease, background 0.2s ease, opacity 0.2s ease;
}
.section-support .support .section-contain .question .question-cards:hover {
  border-color: #2c97e8;
  transform: scale(1.005);
  box-shadow: -2px -2px 20px rgba(0, 0, 0, 0.08);
}
.section-support .support .section-contain .question .question-cards .title {
  color: #455d82;
}
.section-support .support .section-contain .question .question-cards .text-muted {
  color: #9ba0a3;
}
