.section-demo {
  display: flex;
  flex-wrap: wrap;
}
.section-demo .section-item {
  flex: 1;
  min-width: 30%;
  padding: 15px;
  margin: 30px 20px 20px 0;
  border: 1px solid #eee;
}
.section-demo .section-item .title h2 {
  display: inline-block;
}
.section-demo .section-item .title span {
  float: right;
  color: #3090e4;
  background-color: rgba(48, 144, 228, 0.1);
  padding: 0.3em 0.6em 0.3em;
  font-size: 14px;
}
.section-demo .section-item .description {
  font-size: 15px;
  min-height: 100px;
}
