.common-title {
  color: #2c97e8;
  font-size: 42px;
}
.price {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  background: #FFFFFF;
}
.price__describe .title {
  color: #2c97e8;
  font-size: 42px;
}
.price__describe .trial {
  font-size: 120%;
  box-sizing: unset;
  border-radius: 0.4329rem;
}
.price__content .ant-tabs-nav::before {
  border-bottom-color: #2c97e8 !important;
}
.price__content .ant-tabs-tab {
  border-bottom-color: #2c97e8 !important;
}
.price__content .ant-tabs-tab-active {
  background: #2c97e8 !important;
  border: 1px solid #2c97e8 !important;
}
.price__content .ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #fff;
}
.price__content .global-site {
  font-size: 16px;
  color: #9ba0a3;
}
.price__content .price-panel {
  margin-bottom: 2rem;
  padding-left: 20px;
}
.price__content .price-panel .openPanel .price-title:before,
.price__content .price-panel p .price-title:before {
  transform: scale(2.5, 1);
}
.price__content .price-panel .openPanel .accordion-content,
.price__content .price-panel p .accordion-content {
  display: none;
}
.price__content .price-panel .price-title {
  position: relative;
  cursor: pointer;
  font-size: 24px;
  font-weight: 500;
}
.price__content .price-panel .price-title:before {
  display: block;
  content: '';
  position: absolute;
  width: 0.5rem;
  height: 1px;
  background: #000;
  left: -1rem;
  top: 1rem;
  transition: all 0.2s ease;
  transform: rotate(-90deg) scale(2.5, 1);
}
.price__content .price-panel .price-title:hover {
  color: #2c97e8;
}
.price__content .price-panel .accordion-content {
  margin: 20px 0;
  transition: display 0.2s ease;
  font-size: 18px;
}
.price__content .price-panel .accordion-content .accordion-title {
  color: #455d82;
  font-size: 20px;
  margin-bottom: 10px;
}
.price__content .price-panel .accordion-content .table-view {
  width: 100%;
}
.price__content .price-panel .accordion-content .table-view th,
.price__content .price-panel .accordion-content .table-view td {
  border: 1px solid #eaeeef;
  text-align: center;
  padding: 0.5rem 10px;
  font-size: 16px;
  font-weight: 400;
}
.price__content .price-panel .accordion-content .table-view th .title,
.price__content .price-panel .accordion-content .table-view td .title {
  color: #455d82;
  font-size: 18px;
  margin: 0;
}
.price__content .price-panel .accordion-content .table-view th .description,
.price__content .price-panel .accordion-content .table-view td .description {
  font-size: 14px;
  color: #9ba0a3;
  margin: 0;
}
.price__content .price-panel .accordion-content .table-view th .font-logo,
.price__content .price-panel .accordion-content .table-view td .font-logo {
  font-weight: 700;
  margin: 0 0.5rem 0 0;
  font-size: 1.111rem;
}
.price__content .price-panel .accordion-content .table-view th .unit,
.price__content .price-panel .accordion-content .table-view td .unit {
  font-size: 1.111rem;
  margin: 0;
}
.price__content .price-panel .accordion-content .table-view.card-view {
  display: flex;
}
.price__content .price-panel .accordion-content .table-view.card-view td,
.price__content .price-panel .accordion-content .table-view.card-view th {
  flex: 0 1 9rem;
}
.price__content .price-calc-group {
  border: 1px solid #eaeeef;
}
.price__content .price-calc-group .price-calc-title {
  padding: 16px 20px;
}
.price__content .price-calc-group .price-calc-group-body {
  padding: 1em;
}
.price__content .price-calc-group .price-calc-group-body .price-calc,
.price__content .price-calc-group .price-calc-group-body .price-storage {
  display: flex;
  padding: 20px;
  border-top: 1px solid #eaeeef;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-heading,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-heading {
  flex: 1;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-slider,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-slider {
  flex: 2;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-slider .custom-slider,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-slider .custom-slider {
  position: relative;
  background: #fafafa;
  border: 1px solid #d3d3d3;
  box-shadow: inset 0 1px 1px #f0f0f0, 0 3px 6px -5px #bbb;
  height: 1rem;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-slider .custom-slider .bar,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-slider .custom-slider .bar {
  position: absolute;
  background: #2c97e8;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-slider .custom-slider .handle,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-slider .custom-slider .handle {
  position: absolute;
  width: 34px;
  height: 28px;
  left: -17px;
  top: -6px;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
  background: #fff;
  cursor: default;
  outline: 0;
  box-shadow: inset 0 0 1px #fff, inset 0 1px 7px #ebebeb, 0 3px 6px -3px #bbb;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-slider .custom-slider .handle:before,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-slider .custom-slider .handle:before {
  content: '';
  display: inline-block;
  width: 4px;
  height: 100%;
  border-left: 1px solid #e8e7e6;
  border-right: 1px solid #e8e7e6;
  position: absolute;
  left: 40%;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-result,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-result {
  flex: 1;
  text-align: right;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-result input,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-result input {
  border: 0;
  outline: 0;
  text-align: right;
  padding: 2px 4px;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-result input::-webkit-outer-spin-button,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-result input::-webkit-outer-spin-button,
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-result input::-webkit-inner-spin-button,
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-result input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.price__content .price-calc-group .price-calc-group-body .price-calc .price-calc-result input[type='number'],
.price__content .price-calc-group .price-calc-group-body .price-storage .price-calc-result input[type='number'] {
  -moz-appearance: textfield;
}
