.doc.ant-layout {
  background: unset;
}
.doc.ant-layout .ant-layout-header {
  background: #f7f7f7;
}
.doc.ant-layout .ant-layout-header .head_ul {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}
.doc.ant-layout .ant-layout-header .head_ul .head_left {
  width: 60%;
  display: flex;
  justify-content: space-between;
}
.doc.ant-layout .ant-layout-header .head_ul a {
  color: #777;
  font-size: 13px;
}
.doc.ant-layout .ant-layout-header .head_ul a.LeanCloud {
  font-size: 20px;
  font-width: 700;
}
.doc.ant-layout .ant-layout-header .head_ul .log {
  position: absolute;
  right: 0;
}
.doc.ant-layout .algolia-autocomplete {
  width: 1200px;
  text-align: center;
  margin: 0 auto;
}
.doc.ant-layout .algolia-autocomplete .ant-select-show-search {
  min-width: 50%!important;
}
.doc.ant-layout .algolia-autocomplete .ant-select-selector {
  height: 46px;
  line-height: 46px;
  font-size: 16px;
}
.doc.ant-layout .algolia-autocomplete .ant-select-selection-placeholder {
  line-height: 46px;
  text-align: left;
}
.doc.ant-layout .algolia-autocomplete .secTabs .ant-tabs-nav {
  background: #FFFFFF;
  z-index: 999;
}
.doc.ant-layout .algolia-autocomplete .secTabs .ant-tabs-content {
  flex-direction: column;
}
.doc.ant-layout .algolia-autocomplete .secTabs .ant-tabs-content .ant-tabs-tabpane {
  display: block!important;
}
.doc.ant-layout .algolia-autocomplete .secTabs .ant-tabs-content .ant-tabs-tabpane .list-unstyled .ant-btn-primary {
  margin-right: 8px;
  color: #3090e4;
  background: #f1f8ff;
  border-color: #f1f8ff;
}
.doc.ant-layout .algolia-autocomplete .secTabs .ant-tabs-content .ant-tabs-tabpane .list-unstyled .ant-btn-link {
  color: #3090e4;
}
.doc.ant-layout .ant-layout-footer {
  background: unset;
}
