.commn-title {
  font-size: 42px;
  color: #2c97e8;
}
.user-wrap {
  width: 100%;
  background: #FFFFFF;
}
.user-wrap .user-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4.8rem 0;
}
.user-wrap .user-container .user {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.user-wrap .user-container .user .user-container {
  width: 18%;
  margin-right: 20px;
  padding: 0;
}
.user-wrap .user-container .user .text .title {
  font-size: 42px;
  color: #2c97e8;
}
.user-wrap .user-container .user-card {
  display: flex;
  flex-wrap: wrap;
}
.user-wrap .user-container .user-card .card-item {
  flex: 1;
  min-width: 35%;
  border: 1px solid #eaeeef;
  border-radius: 10px;
  margin: 20px;
  padding: 20px 10px;
}
.user-wrap .user-container .user-card .card-item .user-detail {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}
.user-wrap .user-container .user-card .card-item .user-detail .user-logo {
  width: 48px;
  height: 48px;
  border-radius: 10px;
  margin-right: 1rem;
}
.app-wrap {
  width: 100%;
}
.app-wrap .app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4.8rem 0;
  text-align: center;
}
.app-wrap .app-container .title {
  font-size: 42px;
  color: #2c97e8;
}
.app-wrap .app-container .app-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.app-wrap .app-container .app-group img {
  width: 80px;
  height: 80px;
  border-radius: 10px;
  margin-right: 1rem;
  margin: 28px;
}
